<template>
  <div>
    <el-tabs v-model="activeName">
      <el-tab-pane label="上报配置" name="1">
        <div class="flex-center">
          <el-form
            style="width: 38%"
            :model="report_config"
            :rules="rules"
            ref="report_config"
            label-width="140px"
            class="mt-20"
            size="small"
          >
            <el-form-item label="上报类型:" prop="report_type">
              <el-select v-model="report_config.report_type">
                <el-option :value="1" label="立即上报"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="超时时间(秒):" prop="timeout">
              <el-input-number
                controls-position="right"
                v-model="report_config.timeout"
                :min="10"
                :max="60"
              >
              </el-input-number>
            </el-form-item>
            <el-form-item label="上报间隔(秒):" prop="report_interval">
              <el-input-number
                controls-position="right"
                :min="0"
                v-model="report_config.report_interval"
                style="width: 200px"
              />
            </el-form-item>
            <el-form-item>
              <el-button @click="submitConfigForm('report_config')"
                >提交</el-button
              >
            </el-form-item>
          </el-form>
        </div>
      </el-tab-pane>

      <el-tab-pane label="URL配置" name="2">
        <el-button
          class="float-right"
          type="primary"
          size="mini"
          @click="addTheme"
          >添加配置</el-button
        >
        <div>配置列表</div>
        <el-table :data="http_config" style="width: 100%">
          <el-table-column type="index" label="序号" width="200">
          </el-table-column>
          <el-table-column
            prop="configuration_name"
            label="名称"
            width="200"
            align="center"
          >
          </el-table-column>
          <el-table-column
            prop="report_type"
            label="上报属性类型"
            align="center"
          >
            <template slot-scope="scope">{{
              scope.row ? reportTypeList.get(scope.row.report_type) : ""
            }}</template>
          </el-table-column>
          <el-table-column label="操作" align="center">
            <template slot-scope="scope">
              <el-button
                type="text"
                icon="el-icon-edit"
                @click="editTheme(scope.$index)"
              ></el-button>
              <el-button
                style="color: red"
                type="text"
                icon="el-icon-delete"
                @click="delTheme(scope.$index)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
    <el-dialog
      :title="addThemeTitle"
      :visible.sync="addThemeVisible"
      width="50%"
      :close-on-click-modal="false"
    >
      <el-form
        :model="themeForm"
        :rules="rules"
        ref="themeForm"
        label-width="120px"
        class="mt-20"
        size="small"
      >
        <el-form-item label="名称:" prop="configuration_name">
          <el-input v-model="themeForm.configuration_name"></el-input>
        </el-form-item>
        <el-form-item label="上报属性类型:" prop="report_type">
          <el-select v-model="themeForm.report_type">
            <el-option label="属性上报" :value="1"></el-option>
            <el-option label="分析报警" :value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="请求地址:" prop="request_address">
          <el-input
            placeholder="请输入请求地址"
            v-model="themeForm.request_address"
            class="input-with-select"
            prop="hostUrl"
          >
            <el-select
              v-model="themeForm.host_head"
              slot="prepend"
              style="width: 100px"
            >
              <el-option value="http"></el-option>
              <el-option value="https"></el-option>
            </el-select>
          </el-input>
        </el-form-item>
        <el-form-item label="请求方法:" prop="request_method">
          <el-select v-model="themeForm.request_method" style="width: 100px">
            <el-option label="POST" :value="1"></el-option>
            <el-option label="GET" :value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="请求头:" prop="request_header">
          <div
            v-for="(item, index) in requestHeadersList"
            :key="index"
            class="flex-interval mb-10"
          >
            <el-input
              placeholder="请输入参数名"
              v-model="item.key"
              class="mr-10"
            >
              <template slot="prepend">键：</template>
            </el-input>
            <el-input
              placeholder="请输入参数值"
              v-model="item.value"
              class="mr-10"
            >
              <template slot="prepend">值：</template>
            </el-input>
            <el-button
              type="danger"
              icon="el-icon-delete"
              plain
              @click="delParamsItem(index, 'requestHeadersList')"
            ></el-button>
          </div>

          <el-button type="text" @click="addParamsItem('requestHeadersList')"
            >+ 添加请求头</el-button
          >
        </el-form-item>

        <el-form-item label="请求参数:" prop="request_parameters">
          <div
            v-for="(item, index) in requestParamsList"
            :key="index"
            class="flex-interval mb-10"
          >
            <el-input
              placeholder="请输入参数名"
              v-model="item.key"
              class="mr-10"
            >
              <template slot="prepend">键：</template>
            </el-input>
            <el-input
              placeholder="请输入参数值"
              v-model="item.value"
              class="mr-10"
            >
              <template slot="prepend">值：</template>
            </el-input>
            <el-button
              type="danger"
              icon="el-icon-delete"
              plain
              @click="delParamsItem(index, 'requestParamsList')"
            ></el-button>
          </div>

          <el-button type="text" @click="addParamsItem('requestParamsList')"
            >+ 添加参数</el-button
          >
        </el-form-item>
        <el-form-item label="请求体:" prop="request_body">
          <el-input
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 4 }"
            placeholder="请输入内容"
            v-model="themeForm.request_body"
          >
          </el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addThemeVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitThemeForm('themeForm')"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    configForm: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      activeName: "1",
      report_config: {},
      http_config: [],
      rules: {},
      reportTypeList: new Map([
        [1, "属性上报"],
        [2, "属性设置"],
        [3, "分析报警"],
      ]),
      themeForm: {},
      requestHeadersList: [],
      requestParamsList: [],
      addThemeTitle: "新增",
      addThemeVisible: false,
    };
  },
  watch: {
    configForm: {
      handler(val) {
        ({ http_config: this.http_config, report_config: this.report_config } =
          val);
      },
      deep: true,
      immediate: true,
    },
  },
  methods: {
    submitThemeForm() {
      // 参数格式化统一处理
      // 判断是否为数组，如果不是就返回undefined（过滤空值参数，undefined自动引发过滤）
      const formatParams = (list) =>
        Array.isArray(list) && list.length > 0 ? list : undefined;
      this.themeForm.request_header = formatParams(this.requestHeadersList);
      this.themeForm.request_parameters = formatParams(this.requestParamsList);
      let type = this.addThemeTitle == "编辑" ? "edit" : "add";
      if (type == "add") {
        if (
          this.http_config.some(
            (item) => item.report_type === this.themeForm.report_type
          )
        )
          return this.$message.warning("只允许添加一种上报属性类型！");
      }
      this.$emit("submitThemeForm", type, {
        themeForm: this.themeForm,
        ...(type == "edit" && { themeIndex: this.themeIndex }),
        configType: "http_config",
      });
    },
    delParamsItem(index, type) {
      this[type].splice(index, 1);
    },
    addParamsItem(type) {
      this[type].push({});
    },
    delTheme(themeIndex) {
      this.$confirm("是否删除该数据?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$emit("submitThemeForm", "del", {
            themeForm: this.themeForm,
            themeIndex,
            configType: "http_config",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    editTheme(index) {
      this.addThemeVisible = true;
      this.themeIndex = index;
      this.addThemeTitle = "编辑";
      this.themeForm = { ...this.http_config[this.themeIndex] };
      this.requestHeadersList = this.themeForm.request_header || [];
      this.requestParamsList = this.themeForm.request_parameters || [];
    },
    addTheme() {
      this.themeForm = {};
      this.requestHeadersList = [];
      this.requestParamsList = [];
      this.addThemeTitle = "新增";
      this.addThemeVisible = true;
    },
    submitConfigForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$emit("sumbitData", {
            report_config: this.report_config,
            http_config: this.http_config,
          });
        }
      });
    },
    success() {
      this.addThemeVisible = false;
    },
  },
};
</script>

<style lang="scss" scoped></style>
