export function filterEmptyConfig(obj) {
  return Object.entries(obj).reduce((acc, [key, val]) => {
    // 处理数组类型属性
    if (Array.isArray(val)) {
      const filtered = val
        .map((item) =>
          typeof item === "object" ? filterEmptyConfig(item) : item
        )
        .filter((item) => item != null);
      if (filtered.length) acc[key] = filtered;
    }
    // 处理嵌套对象
    else if (val && typeof val === "object") {
      const filtered = filterEmptyConfig(val);
      if (Object.keys(filtered).length) acc[key] = filtered;
    }
    // 处理基础类型值
    else if (val !== "" && val != null) {
      acc[key] = val;
    }
    return acc;
  }, {});
}
export function formatStamp(stamp, fmt) {
  const date = new Date(stamp);
  const opt = {
    "y+": date.getFullYear().toString(), // 年
    "M+": (date.getMonth() + 1).toString(), // 月
    "d+": date.getDate().toString(), // 日
    "h+": date.getHours().toString(), // 时
    "m+": date.getMinutes().toString(), // 分
    "s+": date.getSeconds().toString(), // 秒
  };
  for (const k in opt) {
    const ret = new RegExp("(" + k + ")").exec(fmt);
    if (ret) {
      fmt = fmt.replace(
        ret[1],
        ret[1].length === 1 ? opt[k] : opt[k].padStart(ret[1].length, "0")
      );
    }
  }
  return fmt;
}
