<template>
  <div>
    <el-tabs v-model="activeName" class="mt-10">
      <el-tab-pane label="服务器配置" name="1">
        <div class="flex-center">
          <el-form
            style="width: 35%"
            :model="server_config"
            :rules="rules"
            ref="server_config"
            label-width="120px"
            class="mt-20"
            size="small"
          >
            <el-form-item label="服务器地址:" prop="address">
              <el-input v-model="server_config.address"></el-input>
            </el-form-item>
            <el-form-item label="端口:" prop="port">
              <el-input v-model.number="server_config.port"></el-input>
            </el-form-item>
            <el-form-item label="Client ID:" prop="client_id">
              <el-input v-model="server_config.client_id"></el-input>
            </el-form-item>
            <el-form-item label="用户名:" prop="user_name">
              <el-input v-model="server_config.user_name"></el-input>
            </el-form-item>
            <el-form-item label="密码:" prop="user_pwd">
              <el-input
                v-model="server_config.user_pwd"
                type="password"
                show-password
              ></el-input>
            </el-form-item>
            <el-form-item label="Keep Alive:" prop="keepalive">
              <el-input v-model.number="server_config.keepalive"></el-input>
            </el-form-item>
            <el-form-item label="Qos:" prop="qos">
              <el-radio-group v-model="server_config.qos">
                <el-radio :label="0">0</el-radio>
                <el-radio :label="1">1</el-radio>
                <el-radio :label="2">2</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item>
              <el-button @click="submitConfigForm('server_config')"
                >提交</el-button
              >
            </el-form-item>
          </el-form>
        </div>
      </el-tab-pane>
      <el-tab-pane label="连接通知配置" name="2">
        <div class="flex-center">
          <el-form
            style="width: 35%"
            :model="notice_config"
            :rules="rules"
            ref="notice_config"
            label-width="150px"
            class="mt-20"
            size="small"
          >
            连接通知
            <el-form-item
              class="mt-10"
              label="连接通知主题:"
              prop="notice_topic"
            >
              <el-input v-model="notice_config.notice_topic"></el-input>
            </el-form-item>
            <el-form-item label="连接通知消息:" prop="notice_msg">
              <el-input
                v-model="notice_config.notice_msg"
                type="textarea"
                :rows="2"
              ></el-input>
            </el-form-item>
            遗嘱消息
            <el-form-item
              class="mt-10"
              label="遗嘱消息主题:"
              prop="dyingwords_topic"
            >
              <el-input v-model="notice_config.dyingwords_topic"></el-input>
            </el-form-item>
            <el-form-item label="遗嘱消息Qos:" prop="dyingwords_qos">
              <el-radio-group v-model="notice_config.dyingwords_qos">
                <el-radio :label="0">0</el-radio>
                <el-radio :label="1">1</el-radio>
                <el-radio :label="2">2</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="遗嘱消息保留标志:" prop="dyingwords_retain">
              <el-checkbox
                v-model="notice_config.dyingwords_retain"
                :true-label="1"
                :false-label="2"
              ></el-checkbox>
            </el-form-item>
            <el-form-item label="遗嘱消息:" prop="dyingwords_msg">
              <el-input
                v-model="notice_config.dyingwords_msg"
                type="textarea"
                :rows="2"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button @click="submitConfigForm('notice_config')"
                >提交</el-button
              >
            </el-form-item>
          </el-form>
        </div>
      </el-tab-pane>
      <el-tab-pane label="上报配置" name="3">
        <div class="flex-center">
          <el-form
            style="width: 35%"
            :model="report_config"
            :rules="rules"
            ref="report_config"
            label-width="180px"
            class="mt-20"
            size="small"
          >
            <el-form-item label="上报类型:" prop="report_type">
              <el-select v-model="report_config.report_type">
                <el-option label="立即上报" :value="1"></el-option>
                <el-option label="周期上报" :value="2"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="上报周期:" prop="report_cycle">
              <el-input
                placeholder="请输入内容"
                v-model.number="report_config.report_cycle"
              >
                <template slot="append">秒</template>
              </el-input>
            </el-form-item>
            <el-form-item>
              <el-button @click="submitConfigForm('report_config')"
                >提交</el-button
              >
            </el-form-item>
          </el-form>
        </div>
      </el-tab-pane>
      <el-tab-pane label="主题配置" name="4">
        <el-button
          class="float-right"
          type="primary"
          size="mini"
          @click="addTheme"
          >添加主题</el-button
        >
        <div>主题列表</div>
        <el-table :data="topic_config" style="width: 100%">
          <el-table-column type="index" label="序号" width="100" align="center">
          </el-table-column>
          <el-table-column
            prop="topic_name"
            label="主题"
            align="center"
            width="260"
          >
          </el-table-column>
          <el-table-column prop="topic_type" label="主题类型" align="center">
            <template slot-scope="scope">{{
              reportTypeList.get(scope.row.topic_type)
            }}</template>
          </el-table-column>
          <el-table-column label="操作" align="center">
            <template slot-scope="scope">
              <el-button
                type="text"
                icon="el-icon-edit"
                @click="editTheme(scope.row, scope.$index)"
              ></el-button>
              <el-button
                type="text"
                icon="el-icon-delete"
                style="color: red"
                @click="delTheme(scope.row, scope.$index)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
    <el-dialog
      :title="addThemeTitle"
      :visible.sync="addThemeVisible"
      width="30%"
      :close-on-click-modal="false"
    >
      <div
      class="d-flex f-center"
        style="padding: 10px; background: aliceblue; border: 1px solid #3aacf5"
      >
        <i class="el-icon-warning-outline mr-5 fs-18" style="color: #3aacf5;" ></i
        >可用主题通配符:${tag:key}-数据源标签
      </div>
      <el-form
        :model="themeForm"
        :rules="rules"
        ref="themeForm"
        label-width="120px"
        class="mt-20"
        size="small"
      >
        <el-form-item label="主题:" prop="topic_name">
          <el-input v-model="themeForm.topic_name"></el-input>
        </el-form-item>
        <el-form-item label="上报属性类型:" prop="topic_type">
          <el-select v-model="themeForm.topic_type">
            <el-option label="属性上报" :value="1"></el-option>
            <el-option label="属性设置" :value="2"></el-option>
            <el-option label="分析报警" :value="3"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addThemeVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitThemeForm('themeForm')"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    configForm: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    let validateIP = (rule, value, callback) => {
      const reg =
        /^(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;
      if (!reg.test(value)) {
        callback(new Error("格式错误 格式:XXX.XXX.XXX.XXX"));
      } else {
        callback();
      }
    };
    let validatePort = (rule, value, callback) => {
      if (!(/^[1-9]\d*$/.test(value) && 1 <= 1 * value && 1 * value <= 65535)) {
        callback(new Error("端口为0-65535"));
      } else {
        callback();
      }
    };
    return {
      activeName: "1",
      server_config: {},
      notice_config: {},
      report_config: {},
      topic_config: [],
      rules: {
        apply_name: [{ required: true, message: "请输入", trigger: "blur" }],
        apply_type: [{ required: true, message: "请选择", trigger: "change" }],
        address: [
          { required: true, message: "请输入", trigger: "blur" },
          { validator: validateIP, trigger: "change" },
        ],
        port: [
          { required: true, message: "请输入", trigger: "blur" },
          { validator: validatePort, trigger: "change" },
        ],
        keepalive: [{ required: true, message: "请输入", trigger: "blur" }],
        qos: [{ required: true, message: "请输入", trigger: "blur" }],
        report_type: [{ required: true, message: "请选择", trigger: "change" }],
        report_cycle: [{ required: true, message: "请输入", trigger: "blur" }],
      },
      reportTypeList: new Map([
        [1, "属性上报"],
        [2, "属性设置"],
        [3, "分析报警"],
      ]),
      themeForm: {},
      addThemeTitle: "新增",
      addThemeVisible: false,
    };
  },
  watch: {
    // configForm: {
    //   handler(val) {
    //     ({
    //       server_config: this.server_config,
    //       notice_config: this.notice_config,
    //       report_config: this.report_config,
    //       topic_config: this.topic_config,
    //     } = val);
    //   },
    //   deep: true,
    //   immediate: true,
    // },
    configForm: {
      handler(val) {
        this.server_config = {
          keepalive: val.server_config.server_config || 60,
          qos: val.server_config.qos || 0,
          ...val.server_config,
        };
        this.notice_config = val.notice_config;
        this.report_config = val.report_config;
        this.topic_config = val.topic_config;
      },
      deep: true,
      immediate: true,
    },
  },
  created() {},
  methods: {
    delTheme(themeIndex) {
      this.$confirm("是否删除该数据?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$emit("submitThemeForm", "del", {
            themeForm: this.themeForm,
            themeIndex,
            configType: "topic_config",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    editTheme(item, index) {
      this.themeIndex = index;
      this.addThemeVisible = true;
      this.addThemeTitle = "编辑";
      this.themeForm = { ...this.topic_config[index] };
    },
    submitThemeForm() {
      let type = this.addThemeTitle == "编辑" ? "edit" : "add";
      this.$emit("submitThemeForm", type, {
        themeForm: this.themeForm,
        ...(type == "edit" && { themeIndex: this.themeIndex }),
        configType: "topic_config",
      });
    },
    addTheme() {
      this.themeForm = {};
      this.addThemeTitle = "新增";
      this.addThemeVisible = true;
    },
    submitConfigForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$emit("sumbitData", {
            server_config: this.server_config,
            notice_config: this.notice_config,
            report_config: this.report_config,
            topic_config: this.topic_config,
          });
        }
      });
    },
    success() {
      this.addThemeVisible = false;
    },
  },
};
</script>

<style lang="scss" scoped></style>
