<template>
  <div v-loading="loading">
    <div class="d-flex wrap" v-if="pageShow">
      <div class="dataList" @click="addData">
        <i class="el-icon-plus"></i>添加数据应用
      </div>
      <div class="dataList" v-for="(item, index) in dataList" :key="index">
        <div class="item">
          <div class="flex-interval">
            <div>
              {{ item.apply_name
              }}<i
                class="el-icon-edit el-icon"
                title="编辑"
                @click="editData(item)"
              ></i>
            </div>
            <div class="flex-interval" style="width: 25%">
              <i
                class="el-icon-setting el-icon"
                title="配置"
                @click="configData(item)"
              ></i>

              <i class="el-icon-tickets el-icon" title="日志"></i>
              <i class="el-icon-price-tag el-icon" title="标签"></i>
              <i
                class="el-icon-delete el-icon"
                title="删除"
                style="color: red !important"
                @click="delData(item)"
              ></i>
            </div>
          </div>
          <div class="flex-interval mt-30">
            <div>{{ item.apply_type == 1 ? "mqtt转发" : "http转发" }}</div>
            <el-switch
              v-model="item.apply_status"
              :active-value="1"
              :inactive-value="2"
              @change="changeState(item)"
            >
            </el-switch>
          </div>
        </div>
      </div>
      <el-dialog
        title="添加数据应用"
        :visible.sync="addVisible"
        width="30%"
        :close-on-click-modal="false"
      >
        <el-form
          :model="addForm"
          :rules="rules"
          ref="addForm"
          label-width="120px"
          class="mt-20"
          size="small"
        >
          <el-form-item label="名称:" prop="apply_name">
            <el-input v-model="addForm.apply_name"></el-input>
          </el-form-item>
          <el-form-item label="应用类型:" prop="apply_type">
            <el-select v-model="addForm.apply_type">
              <el-option label="mqtt转发" :value="1"></el-option>
              <el-option label="http转发" :value="2"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addVisible = false">取 消</el-button>
          <el-button type="primary" @click="submitForm('addForm')"
            >确 定</el-button
          >
        </span>
      </el-dialog>
    </div>
    <div v-else>
      <el-page-header @back="pageShow = true" :content="configInfo.apply_name">
      </el-page-header>
      <component
        :key="componentKey"
        :is="componentName"
        :configForm="configForm"
        ref="configRef"
        @sumbitData="submitConfigForm"
        @submitThemeForm="submitThemeForm"
      ></component>
    </div>
  </div>
</template>

<script>
import http from "./components/http.vue";
import Mqtt from "./components/mqtt.vue";
import { filterEmptyConfig } from "../../assets/utils/config";
export default {
  components: {
    http,
    Mqtt,
  },
  data() {
    return {
      dataList: [],
      dataParams: {
        page: 1,
        size: 8,
      },
      addVisible: false,
      addForm: {},
      rules: {
        apply_name: [{ required: true, message: "请输入", trigger: "blur" }],
        apply_type: [{ required: true, message: "请选择", trigger: "change" }],
      },
      pageShow: true,
      activeName: "1",
      server_config: {},
      notice_config: {},
      report_config: {
        timeout: 60,
        report_interval: 60,
      },
      topic_config: [],
      apply_id: "",
      configInfo: {},
      addThemeVisible: false,
      themeForm: {},
      http_config: [],
      addThemeTitle: "新增",
      themeIndex: 0,
      componentKey: 0,
      componentName: "http",
      dataType: new Map([
        [1, "mqtt"],
        [2, "http"],
        [3, "分析报警"],
      ]),
      configForm: {},
      loading: false,
    };
  },
  created() {
    this.getData();
  },
  methods: {
    submitThemeForm(type, data) {
      if (type == "add") {
        this.configForm[data.configType].unshift(data.themeForm);
      } else if (type == "edit") {
        this.configForm[data.configType].splice(
          data.themeIndex,
          1,
          data.themeForm
        );
      } else {
        this.configForm[data.configType].splice(data.themeIndex, 1);
      }
      this.$api
        .post("/apply/setApplyConfig", {
          apply_id: this.configInfo.apply_id,
          apply_config: filterEmptyConfig(this.configForm),
        })
        .then(() => {
          this.$refs.configRef.success();
          this.$message.success("操作成功");
          this.getApplyMqtt();
        });
    },
    getApplyMqtt() {
      this.server_config = {};
      this.notice_config = {};
      this.report_config =
        this.configInfo.apply_type == 1
          ? {}
          : {
              timeout: 60,
              report_interval: 60,
            };
      this.topic_config = [];
      this.http_config = [];

      this.$api
        .get("/apply/getApplyConfig", {
          params: {
            apply_id: this.configInfo.apply_id,
          },
        })
        .then((res) => {
          if (res.data.data) {
            let apply_config = JSON.parse(res.data.data.apply_config || "{}");
            const {
              server_config,
              notice_config,
              report_config,
              topic_config,
              http_config,
            } = apply_config;
            this.configForm = {
              server_config: server_config || {},
              notice_config: notice_config || {},
              report_config: report_config || {},
              topic_config: topic_config || [],
              http_config: http_config || [],
            };
          }
        });
    },

    submitConfigForm(apply_config) {
      this.$api
        .post("/apply/setApplyConfig", {
          apply_id: this.configInfo.apply_id,
          apply_config: filterEmptyConfig(apply_config),
        })
        .then(() => {
          this.$message.success("操作成功");
          this.getApplyMqtt();
        });
    },
    configData(item) {
      this.activeName = "1";
      this.configInfo = { ...item };
      this.getApplyMqtt();

      this.componentName = this.dataType.get(this.configInfo.apply_type);
      this.componentKey = `${this.componentName}_${Date.now()}`;
      this.pageShow = false;
    },
    changeState(item) {
      this.$api
        .post("/apply/setDataApplyStatus", {
          apply_id: item.apply_id,
          apply_status: item.apply_status,
        })
        .then(() => {
          this.$message.success("操作成功");
          this.getData();
        });
    },
    delData(item) {
      this.$confirm("是否删除该数据?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$api
            .post("/apply/delDataApply", {
              apply_id: item.apply_id,
            })
            .then(() => {
              this.$message.success("操作成功");
              this.getData();
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    editData(item) {
      this.addForm = { ...item };
      this.addVisible = true;
    },
    submitForm(formName) {
      this.addForm.apply_status = 1;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.addForm.apply_id) {
            this.$api.post("/apply/updateDataApply", this.addForm).then(() => {
              this.addVisible = false;
              this.$message.success("操作成功");
              this.getData();
            });
          } else {
            this.$api.post("/apply/addDataApply", this.addForm).then(() => {
              this.addVisible = false;
              this.$message.success("操作成功");
              this.getData();
            });
          }
        } else {
          return false;
        }
      });
    },
    addData() {
      this.addForm = {};
      this.addVisible = true;
    },
    getData() {
      this.loading = true;
      this.$api
        .get("/apply/getDataApplyList", { params: this.dataParams })
        .then((res) => {
          this.dataList = res.data.data;
        })
        .finally(() => {
          this.loading = false;
        });
    },
    checkArrayState(arr) {
      if (arr === null) return false;
      if (arr.length === 0) return false;
      if (arr.includes("null")) return false;
      return true;
    },
  },
};
</script>

<style lang="less" scoped>
.dataList {
  width: 30%;
  border: 2px solid #f4f4f4;
  height: 25vh;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin-right: 1%;
  margin-bottom: 10px;
  .item {
    width: 90%;
    height: 80%;
  }
}
::v-deep .el-icon {
  color: #409eff !important;
}
</style>
